<template>
  <div class="quiz">
    <h1>Loot Quiz</h1>

    <div>
      <input type="file" @change="readFileFromInput">
      <p>
        <button v-if="!quiz.inProgress" @click="startQuiz"> Start Quiz </button>
      </p>
    </div>

    <p>
      <template v-if="isLoading">
        ... Loading
      </template>
      <template v-else-if="quiz.inProgress && !isLoading">
        <loot-quiz :questions="quiz.questions"/>
      </template>
    </p>

  </div>
</template>

<script>
  import { 
    ref, reactive, toRefs, computed, watchEffect, watch,
    onBeforeMount, onMounted, onBeforeUpdate, onUpdated, onBeforeUnmount, onUnmounted, 
    onActivated, onDeactivated, onErrorCaptured,
    OnRenderTracked, OnRenderTriggered
  } from 'vue'
  import LootQuiz from '@/components/LootQuiz.vue'

  export default {
    components: { LootQuiz },
    setup(props, context) {
      const { isLoading, file, rawContent, read } = useFileReader()
      const { csv2questions } = useCsvConverter()

      const data = reactive({
        quiz: {
          canInit: false,
          inProgress: false,
          questions: undefined
        }
      })

      watch( rawContent, (newContent, oldContent) => {
        if ( newContent && newContent != oldContent ) {
          data.quiz.questions = csv2questions(newContent)
        }
      })

      function readFileFromInput(event) {
        data.quiz.inProgress = false
        read(event.target.files[0])
      }

      function startQuiz(event) {
        if ( isLoading && !data.quiz.questions ) {
          return  
        }
        data.quiz.inProgress = true
      }

      return { isLoading, ...toRefs(data), readFileFromInput, startQuiz }
    }
  }

  function useFileReader() {
    const data = reactive({
      isLoading: false,
      file: undefined,
      rawContent: undefined
    })

    async function read(file) {
      let reader = new FileReader();
      reader.onloadstart = () => { data.isLoading = true }
      reader.onloadend = (event) => { 
        data.isLoading = false
        data.rawContent = event.target.result
      }
      reader.onerror = (error) => { 
        data.isLoading = false 
        console.error('Error while Loading file: ', error) 
      }

      data.file = file
      await reader.readAsText(file)
    }

    return { ...toRefs(data), read }
  }

  function useCsvConverter() {

    function csv2questions(csv) {
      let questions = []
      let arrCsv = csv.split('\n')

      for (let index in arrCsv) {
        let line = arrCsv[index]
        let arrLine = line.split(';')
        
        switch (arrLine[0].toUpperCase()) {
          case 'MC':
            questions.push(multipleChoice2question(arrLine))
            break
          default: 
            console.error('not able to parse csv line: ', arrLine)
        }
      }

      return shuffle(questions)
    }

    function multipleChoice2question(arr) {
      let question = { 
        type: arr[0],
        question: arr[1],
        answers: []
      }

      for (let i = 2 ; arr[i] ; i += 2) {
        question.answers.push({
          isCorrect: arr[i],
          text: arr[i+1]
        })
      }
      
      question.answers = shuffle(question.answers)

      return question
    }

    return { csv2questions }
  }


  function shuffle(arr) {
    return arr ? arr.sort(() => .5 - Math.random() ) : arr
  }
</script>

<style lang="scss">
body {
  background-color: #aeddff;
}
</style>