<template>
  <p class="question">
    <b>{{ current.index + 1 + ': ' }} {{ current.question.question }} </b>
  </p>

  <div class="answers">
    <template v-for="(answer, index) in current.question.answers" :key="index">
      <div :class="{ answer: true, selected: answer.selected, pos: answer.positive, neg: answer.negative, mis: answer.missed }" @click="handleClickOnAnswer(answer)">
        {{ answer.text }}
      </div>
    </template>
  </div>

  <div :class="{ result: true, pos: current.question.success, neg: !current.question.success && current.question.success != undefined }">
    {{ current.question.success == undefined ? '?' : (current.question.success ? 'Pass' : 'Fail' ) }}
  </div>

  <p class="control">
    <p> <button @click="handleSubmit" :disabled="current.question.submitted"> Submit Answer </button> </p>
    <div class="score"> {{ 'Score: ' + (countPass / (maxIndex / 100)).toFixed(2) + ' %' }} </div>
    <p class="pagination">
      <button @click="setPageAbsolute(0)"> {{ '<<' }} </button>
      <button @click="setPageRelative(-1)"> {{ '<' }} </button>
      <b> &nbsp; {{ current.index + 1 }} / {{ maxIndex+1 }} &nbsp; </b>
      <button @click="setPageRelative(+1)"> {{ '>' }} </button>
      <button @click="setPageAbsolute(maxIndex)"> {{ '>>' }} </button>
    </p>
  </p>

  <p class="overview">
    <template v-for="(question, index) in questions" :key="index">
      <a :class="{ pos: question.success, neg: question.success != undefined && !question.success }" @click="setPageAbsolute(index)"> 
        <b>{{ index + 1 + ': ' }}</b> {{ question.question.substring(0,80) + '...' }} 
      </a> <br/>
    </template>
  </p>

</template>

<script>
  import { 
    ref, reactive, toRefs, computed, watchEffect, watch,
    onBeforeMount, onMounted, onBeforeUpdate, onUpdated, onBeforeUnmount, onUnmounted, 
    onActivated, onDeactivated, onErrorCaptured,
    OnRenderTracked, OnRenderTriggered
  } from 'vue'

  export default {
    props: {
      questions: {
        type: Array,
        required: false,
        default: [{
          type: 'MC',
          question: 'Test Question',
          answers: [
            { isCorrect: true,  text: 'Test Answer 1 (/)' }, 
            { isCorrect: true,  text: 'Test Answer 2 (/)' }, 
            { isCorrect: false, text: 'Test Answer 3 (x)' }, 
            { isCorrect: false, text: 'Test Answer 4 (x)' }
          ]
        }]
      }
    },
    setup(props, context) {
      const data = reactive({
        current: {
          index: undefined,
          question: undefined
        },
        maxIndex: computed(() => props.questions.length - 1),
        countPass: computed(() => props.questions.filter(item => item.success).length ),
        countSubmitted: computed(() => props.questions.filter(item => item.submitted).length )
      })

      function initQuestionByIndex(index) {
        if (index < 0) {
          index = data.maxIndex + 1 + index 
        }
        index = index % (data.maxIndex + 1)
        data.current.index = index
        data.current.question = props.questions[index]
      }

      function setPageAbsolute(index) {
        initQuestionByIndex(index)
      }

      function setPageRelative(nr) {
        let index = data.current.index + nr
        initQuestionByIndex(index)
      }

      function handleClickOnAnswer(answer) {
        answer.selected = !!!answer.selected
      }

      function handleSubmit() {
        let success = true
        data.current.question.submitted = true
        
        for (let answer of data.current.question.answers) {
          let selected = (typeof answer.selected === 'string')? answer.selected.toLowerCase() === 'true' : !!answer.selected
          let isCorrect = (typeof answer.isCorrect === 'string')? answer.isCorrect.toLowerCase() === 'true' : !!answer.isCorrect
          if (isCorrect) {
            answer.positive = true
            answer.negative = false
          }
          else if (selected && !isCorrect) {
            answer.positive = false
            answer.negative = true
          }
          if(selected !== isCorrect) {
            success = false
          }

        }

        data.current.question.success = success
      }

      initQuestionByIndex(0)

      return { ...toRefs(data), setPageRelative, setPageAbsolute, handleClickOnAnswer, handleSubmit }
    }
  }
</script>

<style scoped lang="scss">
  $cg-blue: #0081afff;          /* white */
  $cyan-process: #00abe7ff;     /* white */
  $vivid-sky-blue: #2dc7ffff;   /* white */
  $wheat: #ead2acff;            /* black */
  $sunray: #eaba6bff;           /* black */
  $middle-green: #4B7F52;       /* white */
  $mantis: #7DD181;             /* black */
  $orange-red-crayola: #FE5F55; /* white */
  $border-px: 4px;

  @mixin color-common { color: white; background-color: $cg-blue; }
  @mixin color-answer { color: white; background-color: $cyan-process; }
  @mixin color-tmp { color: inherit; background-color: $vivid-sky-blue; }
  @mixin color-active { 
    color: inherit; 
    background-color: $sunray;
  }
  @mixin color-positive { 
    color: white; 
    background-color: $middle-green; 
    border: $border-px solid $middle-green;
  }
  @mixin color-negative { 
    color: white; 
    background-color: $orange-red-crayola; 
    border: $border-px solid $orange-red-crayola;
  }
  @mixin color-missed { 
    color: white; 
    background-color: $orange-red-crayola;
    border: $border-px solid $middle-green;
  }

  @mixin border {
    border-radius: 10px;
    padding: 10px;
    margin: 10px; 
  }

  .question {
    @include border;
    @include color-common;
  }

  .answers {
    display: inline-grid;
    grid-template-columns: 50% 50%;
  }

  .answer {
    @include border;
    @include color-answer;
    width: 80%;
    position: relative;
    justify-self: center;
    border: $border-px solid rgba(0,0,0,0)
  }
  .answer:hover { @include color-tmp; }
  .answer.selected { @include color-active; }
  .answer.pos { @include color-positive; }
  .answer.neg { @include color-negative; }

  .result {
    font-size: 35px;
    font-weight: 600;
  }
  .result.pos { color: $middle-green; }
  .result.neg { color: $orange-red-crayola; }

  .score {
    font-size: 25px;
    font-weight: 800;
    color: $mantis;
  }

  .control {
    @include border;
    @include color-common;
  }

  .overview {
    text-align: left;
  }
  .overview a {
    position: relative;
    left: 2%;
  }
  .overview a.pos{ color: $middle-green; }
  .overview a.neg{ color: $orange-red-crayola; }

</style>